
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  width: 100%;;
 }
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
    padding: 0;
    margin: 0;
}

 html {
    height: 100%;
    width: 100%;
     position: relative;
}

.main-container {
    width: 100%;
    height: 100%;
    position: absolute;
}



#quote-box {
    width: 500px;
    background: white;
    border: 1px solid #dedede;
    position: absolute;
    left: 50%;
    top: 50%;
    padding: 20px;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    box-shadow: 0px 5px 15px #d6d6d6;
}

#text {
    text-align: center;
    padding: 15px;
    font-size: 18px;

}

#author {
    text-align: right;
    padding-right: 26px;
}

#tweet-quote {
    display: block;
    background-color: black;
    float: left;
    margin: 0 15px
}

 
#new-quote1 {
    width: 60%;
    float: left;
}

.new-quote1,
#new-quote {
    background-color: #926932;
    display: inline-block;
    border: 0;
    font-size: 14px;
    color: white;
    border-radius: 3px;
}

#new-quote {
    text-align: center;
}

.new-quote1 {
    width: 40px;
    height: 40px;
}

#new-quote .new-quote1.addclass {
    width: 120px;
}

#new-quote:hover,
.new-quote1:hover {
    cursor: pointer;
}
.App{width: 100%;
height: 100%;
background-color: black;;}
